import React, { useState } from 'react';
import styled from "@emotion/styled";
import { getYearsDifference, calcBrand, getPlan } from "../Helper";

const Field = styled.div`
display: flex;
margin-bottom: 1rem;
align-items: center;
color: #fff;
`;

const Label = styled.label`
color: #ffff;
flex: 0 0 100px;
`;

const Select = styled.select`
display: block;
width: 100%;
padding: 1rem;
-webkit-appearance: none; 
`;

const InputRadio = styled.input`
margin: 0 1rem;
@media (max-width: 400px) {
    margin: 0 0.2rem;
  }
`;

const Button = styled.button`
background-color: #00838F;
font-size: 16px;
width: 100%;
padding: 1rem;
color: #fff;
text-transform: uppercase;
font-weight: bold;
border: none;
transition: background-color .3s ease;
margin-top: 2rem;
&:hover {
    cursor: pointer;
    background-color: #26C6DA;

}
`;
const Error = styled.div` 
    background-color: red;
    color: white;
    padding: 1rem;
    margin-bottom:2rem;
    width: 95%;
    text-align: center;
    `;

const Form = ({ setSummary, setLoading }) => {
    const [data, setData] = useState({
        brand: ' ',
        year: ' ',
        plan: ''
    });

    const [error, setError] = useState(false);

    //Extraer valores del State datos
    const { brand, year, plan } = data;

    const getInfo = e => {
        setData({
            ...data,
            [e.target.name]: e.target.value
        })
    }
    //Submit del formulario
    const quoteInsurance = e => {
        e.preventDefault();

        if (brand.trim() === '' || year.trim() === '' || plan.trim() === '') {
            setError(true);
            return;
        }
        setError(false);

        //A base of 2000
        let result = 2000;

        //get the difference of years
        let difference = getYearsDifference(year);
        //console.log(difference);
        //For each year we have to subtract 3%
        result -= ((difference * 3) * result) / 100;
        // American 15%
        //Asian 5%
        //European 30%
        result = calcBrand(brand) * result;
        //console.log(result);
        //Basic increases 20%
        //Complete increases 50%
        const increasePlan = getPlan(plan);
        result = parseInt(increasePlan * result).toFixed(2);
        setLoading(true);
        setTimeout(() => {
            setLoading(false);
            setSummary({
                quotation: Number(result),
                data
            });
        }, 2000);
    }

    return (

        <form
            onSubmit={quoteInsurance}
        >
            {error ? <Error>Todos los campos son obligatorios</Error> : null}
            <Field>
                <Label>Marca</Label>
                <Select
                    name="brand"
                    value={brand}
                    onChange={getInfo}
                >
                    <option value="">--- Seleccione ----</option>
                    <option value="american">Americano</option>
                    <option value="european">Europeo</option>
                    <option value="asian">Asiatico</option>
                </Select>
            </Field>
            <Field>
                <Label>A침o</Label>
                <Select
                    name="year"
                    value={year}
                    onChange={getInfo}
                >
                    <option value="">-- Seleccione --</option>
                    <option value="2021">2021</option>
                    <option value="2020">2020</option>
                    <option value="2019">2019</option>
                    <option value="2018">2018</option>
                    <option value="2017">2017</option>
                    <option value="2016">2016</option>
                    <option value="2015">2015</option>
                    <option value="2014">2014</option>
                    <option value="2013">2013</option>
                    <option value="2012">2012</option>
                </Select>
            </Field>
            <Field>
                <Label>Plan</Label>
                <InputRadio
                    type="radio"
                    name="plan"
                    value="Basic"
                    checked={plan === "Basic"}
                    onChange={getInfo}
                />Basico
                <InputRadio
                    type="radio"
                    name="plan"
                    value="Complete"
                    checked={plan === "Complete"}
                    onChange={getInfo}
                />Completo
            </Field>
            <Button
                type="submit"
            >Cotizar </Button>
        </form>
    );
}

export default Form;