import React from 'react';
import styled from "@emotion/styled";

const HeaderContainer = styled.header`
    background-color: rgb(0,0,0,0.3);
    backdrop-filter: blur(80%);
    padding: 10px;
    font-weight: bold;
    color: #FFFF 
`;

const HeaderText = styled.h1`
    color: white;
    font-size: 2rem;
    margin: 10px;
    font-family: "Arial 27px";
    text-align: center;
`;

const Header = ({title} ) => {
    return ( 
        <HeaderContainer>
        <HeaderText>
            {title}
        </HeaderText>
        </HeaderContainer>
     );
}
 
export default Header;